/** @format */
input {
  background-color: #ffc500;
  color: #fff;
  width: auto;
  height: auto;
  border-radius: 7px;
  font-size: large;
  font-family: "Press Start 2P", cursive;
  letter-spacing: 3px;
  text-shadow: 2px -1px #5c553e;
  padding: 10px;
  box-shadow: 3px 5px #c6571b;
  border: none;
  cursor: pointer;
  outline: none;
}

input:active {
  color: #ffc500;
  background-color: #b08d1c;
  box-shadow: 0 2px #ffc500;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  transition-duration: 0.03s;
}

/*header*/
.header ul {
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
    color: #fff;
    background-color: #14343f;
  }
  .header li a {
    display: block;
    padding: 20px 20px;
    border-right: 1px;
    text-decoration: none;
  }
  
  .header li a:hover,
  .header .menu-btn:hover {
    background-color: #f4f4f4;
  }
  
  /* Menu */
  
  .header .menu {
    clear: both;
    max-height: 0;
  }
  
  /* Menu Icon */
  
  .header .menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    padding: 28px 20px;
    position: relative;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    height: auto;
  }
  
  .header .menu-icon .navicon {
    background: #fff;
    display: block;
    height: 4px;
    position: relative;
    transition: background 0.2s ease-out;
    width: 30px;
  }
  
  .header .menu-icon .navicon:before,
  .header .menu-icon .navicon::after {
    background: #fff;
    content: "";
    display: block;
    height: 100%;
    position: absolute;
    transition: all 0.2s ease-out;
    width: 78%;
  }
  
  .header .menu-icon .navicon::after {
    top: -9px;
  }
  
  /* menu btn */
  
  .header .menu-btn {
    display: none;
  }
  
  .header .menu-btn:checked ~ .menu {
    max-height: 440px;
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon {
    background: transparent;
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon::before {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon::after {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }
  
  .header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon::after {
    top: 0;
  }
  
@media (min-width: 48em) {
    .header li {
      float: left;
    }
    .header li a {
      padding: 20px 30px;
    }
    .header .menu-icon {
      display: none;
    }
  }
